<template>
  <div class="main-box">
    <van-form @submit="onSubmit">
      <van-field
        v-model="formData.projectNm"
        required
        label-class="title"
        clearable
        label="项目名称"
        placeholder="请输入项目名称"
        :rules="[{ required: true, message: '请输入项目名称' }]"
      />
      <van-field
        readonly
        v-model="formData.proIndus"
        required
        label-class="title"
        data-name="proIndus"
        label="所属行业"
        placeholder="请选择所属行业"
        is-link
        arrow-direction="down"
        @click="toggleVisible('proIndus')"
        :rules="[{ required: true, message: '请选择所属行业' }]"
      />
      <div class="region-wrapper">
        <picker mode="region" class="picker">
          <div class="pickerMask" @click="toggleVisible('region')"></div>
          <van-field
            class="region"
            readonly
            v-model="formData.region"
            :disabled="!formData.register"
            required
            label-class="title"
            label="公司所在地"
            placeholder="请选择公司所在地"
            is-link
            arrow-direction="down"
            :rules="
              !formData.register
                ? [{ required: true, message: '请选择公司所在地' }]
                : ''
            "
          />
        </picker>
        <van-checkbox
          checked-color="#AB081E"
          icon-size="16px"
          v-model="formData.register"
          label-disabled
          class="checkbox"
          @change="onChange"
          >尚未注册公司</van-checkbox
        >
        <div class="region-line-wrapper">
          <div class="region-line"></div>
        </div>
      </div>
      <van-field
        readonly
        v-model="formData.teamSize"
        required
        label-class="title"
        data-name="teamSize"
        label="团队人数"
        is-link
        placeholder="请选择团队人数"
        arrow-direction="down"
        @click="toggleVisible('teamSize')"
        :rules="[{ required: true, message: '请选择团队人数' }]"
      />
      <van-field
        readonly
        title-width="180rpx"
        required
        v-model="formData.finSt"
        label-class="title"
        data-name="finSt"
        label="企业营收情况"
        placeholder="请选择企业营收情况"
        is-link
        arrow-direction="down"
        @click="toggleVisible('finSt')"
        :rules="[{ required: true, message: '请选择企业营收情况' }]"
      />
      <van-field
        readonly
        v-model="formData.finRound"
        required
        label-class="title"
        data-name="finRound"
        label="融资轮次"
        is-link
        placeholder="请选择融资轮次"
        arrow-direction="down"
        @click="toggleVisible('finRound')"
        :rules="[{ required: true, message: '请选择融资轮次' }]"
      />
      <van-field
        readonly
        required
        title-width="180rpx"
        v-model="formData.quota"
        label-class="title"
        custom-style="margin-bottom: 16rpx;"
        data-name="quota"
        label="期望融资额度"
        is-link
        placeholder="请选择期望融资额度"
        arrow-direction="down"
        @click="toggleVisible('quota')"
        :rules="[{ required: true, message: '请选择期望融资额度' }]"
      />
      <van-field
        v-model="formData.sharesTransfer"
        required
        label-class="title"
        clearable
        type="number"
        label="出让股份%"
        placeholder="请输入大于0到100的数字"
        maxlength="5"
        :rules="[
          {
            required: true,
            message: '请输入大于0到100的数字',
            validator: stValidator
          }
        ]"
      />
      <div class="line"></div>
      <van-field
        class="proDes"
        :autosize="{ minHeight: 100 }"
        type="textarea"
        required
        label-class="title"
        custom-style="flex-direction:
      column;margin-bottom: 16rpx;"
        v-model="formData.proDes"
        placeholder="请详细描述您想请教的问题，并说明项目的主营业务，商业模式，核心优势和壁垒，现阶段经营状况，有利于投资人给出有针对性的反馈。"
        label="项目简介"
        :rules="[{ required: true, message: '请输入项目简介' }]"
      />
      <div class="line"></div>
      <div class="uploader-wrapper">
        <div class="uploader-text">商业计划书上传</div>
        <!-- accept=".pdf,.ppt,.pptx" -->
        <van-uploader
          accept="file"
          name="file"
          v-model="fileList"
          :max-count="1"
          @delete="deleteFile"
          :after-read="afterRead"
          :before-read="beforeRead"
        >
          <van-button
            icon="plus"
            custom-class="upBtn"
            custom-style="border-radius: 8rpx"
          />
        </van-uploader>
      </div>
      <div class="business">
        <!-- <div class="business-text">没有商业计划书</div> -->
        <div class="business-btn-wrapper">
          <van-button
            @click="yuyue"
            color="#AB081E"
            round
            size="small"
            class="business-btn"
          >
            预约修改
          </van-button>
          <div class="business-tip">2021.04.28-2021.05.16 限时免费</div>
        </div>
      </div>
      <div class="line"></div>
      <van-field
        v-model="formData.proUser"
        required
        name="项目负责人"
        label="项目负责人"
        placeholder="请输入项目负责人"
        maxlength="11"
        minlength="11"
        :rules="[{ required: true, message: '请输入项目负责人' }]"
      />
      <van-field
        v-model="formData.proPhonum"
        required
        type="tel"
        name="负责人联系方式"
        label="联系方式"
        placeholder="请输入负责人联系方式"
        :rules="[{ required: true, message: '请输入正确手机号码', validator }]"
      />
      <van-field
        v-model="formData.proEmail"
        required
        name="负责人邮箱"
        label="负责人邮箱"
        placeholder="请输入负责人邮箱"
        :rules="[
          {
            required: true,
            message: '请输入正确的邮箱',
            validator: emailValidator
          }
        ]"
      />
      <div style="margin: 16px" class="btns">
        <van-button
          color="#BEBEBE"
          round
          class="btn"
          native-type="submit"
          @click="btnClick(0)"
        >
          保存草稿
        </van-button>
        <van-button
          color="#AB081E"
          round
          class="btn"
          native-type="submit"
          @click="btnClick(1)"
        >
          选择导师
        </van-button>
      </div>
    </van-form>
    <Popup ref="popup" :status="status" @confirm="confirm" />
  </div>
</template>

<script>
import { defineComponent, onMounted, reactive, ref } from 'vue'
import Popup from '../components/popup.vue'
import {
  upload,
  getdraftbyid,
  getbpApply,
  vipCardUsage,
  browseProjectPage
} from '../api/upload'
import { Toast, Dialog } from 'vant'
import wx from 'weixin-js-sdk'
export default defineComponent({
  name: 'Home',
  components: {
    Popup
  },
  setup() {
    const getUrlParam = (name) => {
      var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)')
      var url = decodeURI(decodeURI(window.location.search)) //获取url中"?"符后的字串，使用了两次decodeRUI解码
      var r = url.substr(1).match(reg) //匹配目标参数
      if (r != null) return unescape(r[2])
      return null //返回参数值
    }

    // const openId = 'on17h4g_rGNrf4QQWITD10haPgoQ'
    const formData = reactive({
      projectNm: '', //项目名称,
      proIndus: '', //所属行业
      region: '', //公司所在地
      teamSize: '', //团队人数
      register: false, //尚未注册公司
      finSt: '', //企业营收情况
      finRound: '', //融资轮次
      quota: '', //期望融资额度
      proDes: '', //项目简介
      proUser: '', //项目负责人
      proPhonum: '', //联系方式
      entProvince: '',
      entCity: '',
      proEmail: '', //联系邮箱
      sharesTransfer: '' //出让股份
    })
    const telStr = /^[1](([3][0-9])|([4][0,1,4-9])|([5][0-3,5-9])|([6][2,5,6,7])|([7][0-8])|([8][0-9])|([9][0-3,5-9]))[0-9]{8}$/
    const emailStr = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
    const validator = (val) => telStr.test(val)
    const stValidator = (val) => Number(val) > 0 && Number(val) <= 100
    const emailValidator = (val) => emailStr.test(val)
    const openId = ref('')
    const phoneNm = ref('')
    const bpApplyTimes = ref('')
    const commentTimes = ref('')
    const onChange = () => {
      if (formData.register) {
        formData.region = ''
      }
    }
    onMounted(async () => {
      openId.value = getUrlParam('openId') || 'on17h4g_rGNrf4QQWITD10haPgoQ'
      phoneNm.value = getUrlParam('phoneNm')
      const res = await getdraftbyid({ openId: openId.value })
      const resVip = await vipCardUsage(openId.value)
      const data = new FormData()
      data.append('openId', openId.value)
      await browseProjectPage(data)
      if (resVip.data.code === 200) {
        bpApplyTimes.value = resVip.data.data.bpApplyTimes
        commentTimes.value = resVip.data.data.commentTimes
      }
      if (res.data.code === 200) {
        if (res.data && res.data.data) {
          formData.projectNm = res.data.data.projectNm
          formData.proIndus = res.data.data.proIndus
          formData.region = res.data.data.entProvince + res.data.data.entCity
          formData.teamSize = res.data.data.teamSize
          formData.finSt = res.data.data.finSt
          formData.finRound = res.data.data.finRound
          formData.quota = res.data.data.quota
          formData.proDes = res.data.data.proDes
          formData.proUser = res.data.data.proUser
          formData.proPhonum = res.data.data.proPhonum
          formData.register = !res.data.data.isRegister
          formData.entProvince = res.data.data.entProvince
          formData.entCity = res.data.data.entCity
          formData.proEmail = res.data.data.proEmail
          formData.sharesTransfer = Number(res.data.data.sharesTransfer)
        }
      } else {
        Toast(res.data.message)
      }
    })
    const popup = ref()
    const status = ref('')
    const toggleVisible = (val = '') => {
      if (val === 'region' && formData.register) {
        return
      }
      popup.value.onShow()
      status.value = val
    }
    const confirm = (status, value) => {
      if (status === 'proIndus') {
        formData.proIndus = value[1].text
      } else if (status === 'region') {
        formData.entProvince = value[0].name
        formData.entCity = value[1].name
        formData[status] = value[0].name + value[1].name
      } else {
        formData[status] = value
      }
    }
    const fileList = ref([])
    const file = ref({})
    const afterRead = (objFile) => {
      console.log(objFile)
      file.value = objFile
      fileList.value = [objFile]
    }
    const beforeRead = (file) => {
      // .pdf,.ppt,.pptx
      const name = file.name
      if (
        name.endsWith('.pdf') ||
        name.endsWith('.ppt') ||
        name.endsWith('.pptx')
      ) {
        return true
      } else {
        Toast('请上传pdf或者ppt文件')
        return false
      }
    }
    const deleteFile = () => {
      file.value = []
      fileList.value = []
    }
    let isDone = false
    const btnClick = (val) => {
      isDone = val
    }
    const yuyue = () => {
      if (!formData.projectNm) {
        Toast('请输入项目名称')
        return
      }
      if (!formData.proIndus) {
        Toast('请选择所属行业')
        return
      }
      if (!formData.region) {
        Toast('请选择公司所在地')
        return
      }
      if (!formData.teamSize) {
        Toast('请选择团队人数')
        return
      }
      if (!formData.finSt) {
        Toast('请选择企业营收情况')
        return
      }
      if (!formData.finRound) {
        Toast('请选择融资轮次')
        return
      }
      if (!formData.quota) {
        Toast('请选择期望融资额度')
        return
      }
      if (!formData.sharesTransfer) {
        Toast('请输入出让股份')
        return
      }
      if (formData.sharesTransfer <= 0 || formData.sharesTransfer > 100) {
        Toast('出让股份数量为0-100')
        return
      }
      if (!formData.proDes) {
        Toast('请输入项目简介')
        return
      }
      if (bpApplyTimes.value === 0) {
        Dialog.confirm({
          title: '已用完',
          message: '修改BP次数已用完,是否需要购买次数'
        })
          .then(() => {
            wx.miniProgram.navigateTo({
              url: '/pages/main/vipdetail/vipdetail'
            })
          })
          .catch(() => {
            // on cancel
          })
        return
      }

      getbpApply({
        ...formData,
        ...{ phoneNm: phoneNm.value, openId: openId.value }
      }).then(() => {
        Dialog.confirm({
          message:
            '本服务由企融星平台联合“沧澜资本”提供（服务范围包括：洽谈沟通，给出商业计划书优化修改建议）。沧澜资本提出任何后续收费服务与企融星平台无关。'
        })
          .then(() => {
            Dialog({
              message: '预约成功'
            })
          })
          .catch(() => {})
      })
    }
    const onSubmit = async () => {
      Toast.loading({
        duration: 0,
        forbidClick: true,
        loadingType: 'spinner',
        message: '提交中....'
      })
      const data = new FormData()
      data.append('file', file.value.file)
      data.append('projectNm', formData.projectNm)
      data.append('proIndus', formData.proIndus)
      data.append('entProvince', formData.entProvince)
      data.append('entCity', formData.entCity)
      data.append('teamSize', formData.teamSize)
      data.append('isRegister', !formData.register)
      data.append('finSt', formData.finSt)
      data.append('finRound', formData.finRound)
      data.append('quota', formData.quota)
      data.append('sharesTransfer', Number(formData.sharesTransfer))
      data.append('proDes', formData.proDes)
      data.append('proUser', formData.proUser)
      data.append('proPhonum', formData.proPhonum)
      data.append('proEmail', formData.proEmail)
      data.append('openId', openId.value)
      data.append('isDone', isDone === 1 ? true : false)
      data.append('phoneNm', phoneNm.value)
      const res = await upload(data)
      Toast.clear()
      if (res.data.code === 200) {
        if (isDone === 1) {
          wx.miniProgram.navigateTo({
            url: `/pages/main/pay/pay?projectNm=${res.data.data.projectNm}&projectNo=${res.data.data.projectNo}&bpApplyTimes=${bpApplyTimes.value}&commentTimes=${commentTimes.value}`
          })
        } else {
          Toast(res.data.message)
        }
      } else {
        Toast(res.data.message)
      }
    }
    return {
      onSubmit,
      formData,
      toggleVisible,
      status,
      confirm,
      fileList,
      deleteFile,
      afterRead,
      popup,
      btnClick,
      validator,
      stValidator,
      yuyue,
      beforeRead,
      onChange,
      emailValidator
    }
  }
})
</script>

