<template>
  <van-popup
    v-model:show="show"
    position="bottom"
    :style="{ height: '40%' }"
    :close-on-click-overlay="false"
  >
    <van-area
      v-if="status === 'region'"
      :area-list="data"
      :columns-num="2"
      @confirm="onConfirm"
      @cancel="onCancel"
    />
    <van-picker
      v-else
      :columns="data"
      @confirm="onConfirm"
      @cancel="onCancel"
    />
  </van-popup>
</template>
<script>
import { computed, defineComponent, ref } from 'vue'
import immutableData from '../utils/const'
import { areaList } from '@vant/area-data'
export default defineComponent({
  name: 'popup',
  emits: ['close', 'confirm'],
  props: {
    status: {
      type: String
    }
  },
  setup(props, context) {
    const show = ref(false)
    const onShow = () => {
      show.value = true
    }
    const onCancel = () => {
      show.value = false
    }
    const data = computed(() => {
      if (props.status === 'region') {
        return areaList
      } else {
        return immutableData[props.status]
      }
    })
    const onConfirm = (value) => {
      context.emit('confirm', props.status, value)
      show.value = false
    }
    return {
      show,
      data,
      onConfirm,
      onCancel,
      onShow
    }
  }
})
</script>